import {
    PRODUCT_FINDER_SUCCESS,
    PRODUCT_FINDER_FAIL,
    LOWEST_OFFERS_SUCCESS,
    LOWEST_OFFERS_FAIL,
    ADD_PRODUCT_SUCCESS,
    ADD_PRODUCT_FAILED
} from '../actions/types';

// Initial state of the profile reducer. By default, all fields are set to null/false.
const initialState = {
    data: {},
    offers: {},
    products:{}
}

export default function (state = initialState, action) {

    switch (action.type) {

        case PRODUCT_FINDER_SUCCESS:

            return {
                ...state,
                data: action.payload
            }

        case PRODUCT_FINDER_FAIL:

            return {
                ...state,
                data: action.payload
            }

        case LOWEST_OFFERS_SUCCESS:

            return {
                ...state,
                offers: action.payload
            }

        case LOWEST_OFFERS_FAIL:

            return {
                ...state,
                offers: action.payload
            }

        case ADD_PRODUCT_SUCCESS:

            return {
                ...state,
                products:action.payload
            }

        case ADD_PRODUCT_FAILED:

            return {
                ...state,
                products:action.payload
            }

        default:
            return state;
    }
}